*,
*:before,
*:after {
  box-sizing: border-box;
}
body,
h1,h2,h3,h4,h5,h6,
p,
ul,li {
  margin: 0;
  padding: 0;
  color: inherit;
}
body {
  height: 100%;  
  font-family: Arial, serif;
  font-size: 16px;
  font-weight: 400;
  color: #444;
}
a {
  text-decoration: none;
  color: inherit;
}
input,
button,
textarea,
select {
  font: inherit;
}
ul,li {
  list-style: none;
  padding: 0;
}
img {
  display: block;
}

:root {
  --main-color: rgb(85, 130, 214);
  --second-color: rgb(68, 113, 197);
  --bgc: rgb(245, 245, 239);
}

.app {
  display: flex;
  flex-direction: column;
  gap: 20px;
  height: 100vh;
  background: linear-gradient(rgb(245, 244, 255), rgb(255, 255, 255)) no-repeat;
}

::-webkit-scrollbar {
  width: 8px;
}
::-webkit-scrollbar-track {
  background: #e4e4e4;
}
::-webkit-scrollbar-thumb {
  background-color: #adadad;
}